<template>
  <div>
    <form @submit.prevent="submit">
      <div>
        <label for="">Username Or Email</label>
        <input
          type="text"
          id="username"
          name="username"
          v-model="form.username"
        />
      </div>
      <div>
        <label for="">Password</label>
        <input
          type="password"
          id="password"
          name="password"
          v-model="form.password"
        />
      </div>
      <div>
        <button type="submit">SignIn</button>
      </div>
    </form>
  </div>
</template>

<script>
// @ is an alias to /src
import { mapActions } from "vuex";
export default {
  name: "SignIn",
  data: () => {
    return {
      form: {
        username: "",
        password: ""
      }
    };
  },
  methods: {
    ...mapActions({
      signIn: "auth/signIn"
    }),
    submit() {
      this.signIn(this.form).then(() => {
        this.$router.replace({
          name: "Dashboard"
        });
      });
    }
  }
};
</script>
